<template>
  <div class="relative">
    <div class="vx-navbar-wrapper nav-menu-wrapper">
      <vs-navbar class="vx-navbar navbar-custom navbar-skelton" :color="navbarColor">

        <ul class="menu-items flex flex-wrap w-full items-center">
          <li
            class  = "menu-item"
            v-for  = "(item, index) in navMenuItems"
            :key   = "index"
            :class = "{'mr-2': !(navMenuItems.length === index+1)}">

            <!-- If header -->
            <template v-if="item.header">
              <h-nav-menu-header :header="item" class="menu-header relative" />
            </template>

            <!-- If it's group -->
            <template v-else-if="item.submenu">
              <h-nav-menu-group
                class="menu-group relative py-4"
                bottom
                :key        ="`group-${index}`"
                :group      ="item"
                :groupIndex ="index"
                :open       ="checkGrpChildrenActive(item)" />
            </template>

            <!-- If it's link -->
            <div v-else-if="item.url" class="menu-link">
              <h-nav-menu-item
                class       = "relative py-4 cursor-pointer"
                :to         = "item.slug !== 'external' ? item.url : null"
                :href       = "item.slug === 'external' ? item.url : null"
                :icon       = "item.icon"
                :target     = "item.target"
                :isDisabled = "item.isDisabled"
                :slug       = "item.slug">
                  <span class="truncate">{{ $t(item.i18n) || item.name }}</span>
                  <vs-chip :color="item.tagColor" v-if="item.tag">{{ item.tag }}</vs-chip>
              </h-nav-menu-item>
            </div>

          </li>
        </ul>
        <!-- <vs-button id="subscription-status-btn" type="border" style="width: 150px; text-align: center">
          <div v-if="!loading">
            <div v-if="hasSubsections">
            <span v-if="remainingDays > 0">
              {{remainingDays}} روز
            </span>
              <span v-else>
              اتمام اشتراک
            </span>
            </div>
            <span v-else>
            اشتراک ندارید
          </span>
          </div>
          <div v-else style="direction: ltr">
            Loading...
          </div>
        </vs-button> -->
        <vs-button @click="logout()" type="border" class="ml-4" style="text-align: center">
          Logout
        </vs-button>
      </vs-navbar>
    </div>
  </div>
</template>

<script>
import HNavMenuGroup  from './HorizontalNavMenuGroup.vue'
import HNavMenuHeader from './HorizontalNavMenuHeader.vue'
import HNavMenuItem   from './HorizontalNavMenuItem.vue'
import mixins from '../../../mixins'
import axios from '../../../http/axios'

export default {
  mixins: [mixins],
  props: {
    // navbarColor  : { type: String, default: "#fff", },
    navMenuItems : { type: Array,  required: true   }
  },
  components: {
    HNavMenuGroup,
    HNavMenuHeader,
    HNavMenuItem
  },
  computed: {
    navbarColor () {
      return this.$store.state.theme === 'dark' ? '#10163a' : '#fff'
    }
  },

  data(){
    return {
      remainingDays: null,
      hasSubsections: false,
      loading: true,
    }
  },

  mounted () {
    this.getUserPackages();
  },

  methods: {
    checkGrpChildrenActive (group) {
      const path        = this.$route.fullPath
      let active        = false
      const routeParent = this.$route.meta ? this.$route.meta.parent : undefined

      if (group.submenu) {
        group.submenu.forEach((item) => {
          if (active) return true
          if ((path === item.url || routeParent === item.slug) && item.url) { active = true } else if (item.submenu) { this.checkGrpChildrenActive(item) }
        })
      }

      return active
    },
    getUserPackages(){
      this.loading = true;
      axios.get(`/api/store/user-packages/`,{
        headers:{
          Accept: "application/json",
          Authorization: this.getAccessToken()
        }
      }).then(res => {
        if (res.status === 200){
          res.data.forEach(item => {
            if (item.package.type.id === this.SUBSCRIPTION_TYPE){
              this.hasSubsections = true;
              this.remainingDays = Math.ceil((Date.parse(item.expires_at) - Date.now()) / 1000 / 60 / (60 * 24));
            }
          })
        }else {
          this.simpleNotify(this.NOTIFY_DANGER_TITLE, 'Something goes wrong', 'danger');
        }
        this.loading = false;
      })
    },
  }
}
</script>

<style lang="scss">
@import "@/assets/scss/vuexy/components/horizontalNavMenu.scss"
</style>
